import '../../static/common/share.css'
import states from '../../components/poatdetails/states.vue'
import api from '../../api/api.js'
import userSare from '../../components/share/share.vue'
import emptyComments from '../../components/poatdetails/emptycomments.vue'
export default {
	props: ["judiflag","detail","comments"],
	components: {
		states, userSare, emptyComments
	},
	watch: {
		commentScroll(newV, oldV) {
			if (newV != oldV) {
				this.toView = ''
			}
		}
	},
	data() {
		return {
			toView: '',
			commentScroll: 0,
			statesH: '',
			content: '',
			keyComment: '',
			replayContent: '',
			is_collect: 1,
			is_zan: 1,
			shadowFn: false,
			replayFn: false,
			replayComments: {}, // 回复评论详情
			replyItems: [], // 回复评论详情列表
			replayPlace: '写回复...', // 灰色评论placeholder
			replayId: 0, // 回复用户id
			replayFocus: false, // 评论回复按钮焦点判断
			commentFocus: false, // 
			replayPage: 1, // 回复页码
			commentPage: 1, // 评论页码
			commentLoad: true, // 评论触底
			replayLoad: true, // 回复触底
			replaynotice: '评论已经加载完了', // 回复底部提示
			totalComments:this.detail.comments,
			commentsList:[],
			ifDianZan: true, // 点赞 取消赞加锁
			isIOS: 0,
			is_weixin: 0,
			// 分享用到的
			shaflag: false,
			isShowBottom: true,
			isBackHome: 0,
			
			showPOP: 0,
			chattitle: '',
			chatContent: '',
			comreNotice: '',
			
			btn_bottom_like:require("../../static/mine_img/btn_bottom_like@2x.png"),
		}
	},
	// onLoad(options) {
	// //如果url中存在uid,则此链接为分享链接，需留记录
	//       if(options.uid){
	//         api.clickshare({
	//           'type' : 'center',
	//           'news_id' :  options.news_id,
	//           'uid' : options.uid
	//         }, function(res) {
	//           console.log(res)
	//         })
	//       }
	// },
	created() {
		// console.log(123)








	},
	mounted() {
		let curPage = this.getCurPage()
		let curParam = curPage.options || curPage.$route.query;
		// console.log(curParam)
		if(curParam.isBackHome == 1){
			this.isBackHome = 1
		}
		this.commentsList = this.comments;
		this.content = this.detail.content.replace(/\n/g,"<br/>")
		this.is_zan = this.detail.is_zan





	},
	methods: {
		showChatBox(e) {
			this.showPOP = 1
			if(e == 0){
				this.chattitle = '评论'
				this.comreNotice = '请输入...'
			}else {
				this.chattitle = '回复'
				this.comreNotice = '请输入...'
			}
		},
		cancelFn() {
			this.showPOP = 0
		},
		sendFn() {
			this.showPOP = 0
			if(this.chattitle == '评论'){
				this.keyComment = this.chatContent
				setTimeout(() => {
					this.chatContent = ''
				}, 100);
				this.onKeyComment()
			}else {
				this.replayContent = this.chatContent
				setTimeout(() => {
					this.chatContent = ''
				}, 100);
				this.onKeyReply()
			}
		},
		// 分享落地页返回首页
		backToHome() {





			uni.navigateTo({
				url: '/pages/index/index',
			});

		},
		// 组件获取页面路由参数
		getCurPage() {
			let pages = getCurrentPages();
			let curPage = pages[pages.length-1];
			return curPage
		},
		back() {




			uni.navigateBack({
				delta: 1
			})

		},
		// 接收子组件传来的高度
		statesHeight(e) {
			this.statesH = e
		},
		returnComment() {
			this.toView = 'comment'
		},
		turnScroll(e) {
			this.commentScroll = e.detail.scrollTop
		},
		openReply(id) {
			this.isShowComment = false
			this.replayPlace = '写回复...'
			this.replayCommentId = id
			let page = this.replayPage = 1
			var commentsList = this.commentsList;
			api.tcinfcentercommentdetail({
				'comment_id': this.replayCommentId,
				'page': page
			}, res => {
				if(res.error == 0){
					for (var k = 0; k < commentsList.length; k++) {
						if(commentsList[k].comment_id == id){
							commentsList[k].reply_count = res.data.comment.reply_count;
						}
					}
					this.commentsList = commentsList;
					if(res.data.items.length > 0){
						this.isReply = true
						this.replayComments = res.data.comment
						this.replyItems = res.data.items
						this.replayLoad = true
						this.scrolltoGetlower()
					}else{
						this.replayComments = res.data.comment
						this.replyItems = []
						this.replayLoad = false
						this.replaynotice = '评论已经加载完了~'
					}
				}
			})
			this.shadowFn = true
			this.replayFn = true
		},
		closePop() {
			this.shadowFn = false
			this.replayFn = false
		},
		// 分享
		moreShare() {
			if(this.isIOS == 0){
				this.shaflag = true



			}else {
				// alert(this.isIOS + '23333')













			}
		},
		closeShaflag(e) {
			this.shaflag = e
		},










		// 删除自己的评论
		dropComments(id) {
			uni.showModal({
				content: "是否确认删除该评论",
				success: (e) => {
					if (e.confirm) {
						api.tcinfcentercommentdelete({
							'type': 1,
							'comment_id': id
						}, res => {
							if(res.error == 0){
								uni.showToast({
									icon: 'none',
									title: '删除成功',
									duration: 1500
								})
								setTimeout(() => {
									if(this.commentsList.length == 1){
										this.commentsList = []
										this.getPostComment()
									}else{
										this.getPostComment()
									}
								}, 1200);
							}else {
								uni.showToast({
									icon: 'none',
									title: res.message
								})
							}
						})
					}
				}
			});
		},
		// 删除自己的回复
		dropReplays(id) {
			uni.showModal({
				content: "是否确认删除该回复",
				success: (e) => {
					if (e.confirm) {
						api.tcinfcentercommentdelete({
							'type': 2,
							'reply_id': id
						}, res => {
							if(res.error == 0){
								uni.showToast({
									icon: 'none',
									title: '删除成功',
									duration: 1500
								})
								setTimeout(() => {
									if (this.replyItems.length == 1) {
										this.openReply(this.replayCommentId)
										this.replyItems = []
										this.getPostComment()
									} else if (this.replyItems.length > 1) {
										this.openReply(this.replayCommentId)
									}
								}, 1200)
							}else {
								uni.showToast({
									icon: 'none',
									title: res.message
								})
							}
						})
					}
				}
			});
		},
		
		// 帖子底部评论
		onKeyComment() {
			if(this.keyComment != ''){
				api.tcinfcentercomment({
					'news_id': this.detail.news_id,
					'content': this.keyComment
				}, res => {
					if(res.error == 0){
						uni.showToast({
							icon: 'none',
							title: '评论成功'
						})
						this.getPostComment()
						this.keyComment = ''
						// this.commentFocus = true
						setTimeout(() => {
							this.commentFocus = false
						}, 1)
					}else {
						uni.showToast({
							icon: 'none',
							title: res.message
						})
						// this.commentFocus = true
						setTimeout(() => {
							this.commentFocus = false
						}, 1)
					}
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '评论内容不能为空'
				})
			}
		},
		// 获取帖子内评论详情
		getPostComment() {
			let page = this.commentPage = 1
			api.tcinfcentercommentitems({
				'news_id': this.detail.news_id,
				'page': page
			}, res => {
				if(res.error == 0){
					this.totalComments = res.data.comments
					if(res.data.items.length == 0){
						this.commentLoad = false
					}else{
						this.commentLoad = true
						this.commentsList = res.data.items
					}
				}
			})
		},
		// 评论触底
		scrolltoGetlowerComment() {
			// if(this.postDetails.closed == 0 && this.postDetails.audit == 1){
				if(this.commentLoad == true){
					let page = this.commentPage
					page++
					api.tcinfcentercommentitems({
						'news_id': this.detail.news_id,
						'page': page
					}, res => {
						if(res.error == 0){
							this.totalComments = res.data.comments
							if(res.data.items.length == 0){
								this.commentLoad = false
							}else{
								this.commentLoad = true
								this.commentPage = page
								this.commentsList = this.commentsList.concat(res.data.items)
							}
						}
					})
				}
			// }
		},
		// 回复触底加载
		scrolltoGetlower() {
			if(this.replayLoad == true){
				let page = this.replayPage
				page++
				api.tcinfcentercommentdetail( {
					'comment_id': this.replayCommentId,
					'page': page
				}, res => {
					// console.log(res)
					if(res.error == 0){
						if(res.data.items.length > 0){
							this.isReply = true
							this.replayComments = res.data.comment
							this.replayLoad = true
							this.replyItems = this.replyItems.concat(res.data.items)
							this.replayPage = page
						}else{
							this.replayLoad = false
							this.replaynotice = '评论已经加载完了~'
						}
					}
				})
			}
		},
		// 点击用户评论回复楼主
		replayLz() {
			this.replayFocus = false
			this.replayId = 0
			this.replayPlace = '回复楼主'
			setTimeout(() => {
				this.replayFocus = true
			}, 1)
		},
		onKeyReplyWord(e) {
			this.replayContent = e.detail.value
		},
		onKeyCommentWord(e) {
			this.keyComment = e.detail.value
		},
		// 灰色评论区弹出层回复评论
		onKeyReply(){
			// 判断是回复楼主还是回复其他人 0 回复楼主 其他 回复对应人员
			if(this.replayId == 0){
				api.tcinfcentercommentreply({
					'comment_id': this.replayComments.comment_id,
					'content': this.replayContent
				}, res => {
					if(res.error == 0){
						uni.showToast({
							icon: 'none',
							title: '回复评论成功',
							duration: 1500
						})
						this.openReply(this.replayComments.comment_id)
						this.replayContent = ''
						this.replayId = 0
						this.replayPlace = '写回复...'
						this.replayFocus = false
					}else{
						uni.showToast({
							icon: 'none',
							title: res.message
						})
					}
				})
			}else{
				api.tcinfcentercommentreply({
					'comment_id': this.replayComments.comment_id,
					'content': this.replayContent,
					'to_reply_id': this.replayId
				}, res => {
					if(res.error == 0){
						uni.showToast({
							icon: 'none',
							title: '回复评论成功',
							duration: 1500
						})
						this.openReply(this.replayComments.comment_id)
						this.replayContent = ''
						this.replayId = 0
						this.replayPlace = '写回复...'
						this.replayFocus = false
					}else{
						uni.showToast({
							icon: 'none',
							title: res.message
						})
					}
				})
			}
		},
		// 点击灰色评论区用户评论触发
		transmit(id, name, me){
			if(this.detail.closed == 0){
				if(me == 0){
					// 用户触发的不是自己的回复

					this.replayFocus = false
					this.replayId = id
					this.replayPlace = '回复 ' + name
					setTimeout(() => {
						this.replayFocus = true
					}, 1)










				}else {
					uni.showToast({
						title: '不可以回复自己的回复',
						icon: 'none'
					});
				}
			}
		},
		// 用户点赞
		chZan() {
			if (this.is_zan == 0 && this.ifDianZan == true) {
				api.tcinfcenterdianzan({
					'news_id': this.detail.news_id,
					'status': 1
				}, res => {
					this.ifDianZan = false
					if (res.error == 0) {
						uni.showToast({
							icon: 'none',
							title: '点赞成功',
							duration: 1500
						})
						setTimeout(() => {
							this.ifDianZan = true
						}, 1500)
						this.is_zan = 1
					} else {
						uni.showToast({
							icon: 'none',
							title: res.message,
							duration: 1500
						})
						setTimeout(() => {
							this.ifDianZan = true
						}, 1500)
					}
				})
			} else if(this.is_zan == 1 && this.ifDianZan == true) {
				api.tcinfcenterdianzan({
					'news_id': this.detail.news_id,
					'status': 0
				}, res => {
					this.ifDianZan = false
					if (res.error == 0) {
						uni.showToast({
							icon: 'none',
							title: '取消点赞成功',
							duration: 1500
						})
						setTimeout(() => {
							this.ifDianZan = true
						}, 1500)
						this.is_zan = 0
					} else {
						uni.showToast({
							icon: 'none',
							title: res.message,
							duration: 1500
						})
						setTimeout(() => {
							this.ifDianZan = true
						}, 1500)
					}
				})
			}
		},
	}
}
